import EventDispatcher from 'happens';
import DRACOLoader from '../../lib/three/examples/DRACOLoader';
import { THIRD_PARTY } from '../../constants';

const dracoDecoderType = {};

const DRACO_DECODER_WASM = `${THIRD_PARTY}draco/draco_decoder.wasm`;
const DRACO_DECODER_WASM_WRAPPER = `${THIRD_PARTY}draco/draco_wasm_wrapper.js`;
const DRACO_DECODER = `${THIRD_PARTY}draco/draco_decoder.js`;

export default class DracoLoader {
  constructor(asset) {
    EventDispatcher(this);
    this.asset = asset;
  }

  setup() {
    return new Promise((resolve, reject) => {
      if (typeof WebAssembly !== 'object') {
        this._loadJavaScriptFile(DRACO_DECODER).then(resolve).catch(reject);
      } else {
        this._loadJavaScriptFile(DRACO_DECODER_WASM_WRAPPER)
          .then(() => {
            this._loadWebAssemblyDecoder().then(resolve).catch(reject);
          })
          .catch(reject);
      }
    });
  }

  _loadJavaScriptFile(file) {
    return new Promise(resolve => {
      const head = document.getElementsByTagName('head')[0];
      const element = document.createElement('script');
      element.type = 'text/javascript';
      element.src = file;
      element.onload = () => {
        resolve();
      };

      head.appendChild(element);
    });
  }

  _loadWebAssemblyDecoder() {
    return new Promise(resolve => {
      dracoDecoderType.wasmBinaryFile = DRACO_DECODER_WASM;
      const req = new XMLHttpRequest();
      req.open('GET', DRACO_DECODER_WASM, true);
      req.responseType = 'arraybuffer';

      req.onreadystatechange = () => {
        if (req.readyState === 4) {
          if (req.status === 200) {
            // draco_wasm_wrapper.js must be loaded before DracoModule is
            // created. The object passed into DracoModule() must contain a
            // property with the name of wasmBinary and the value must be an
            // ArrayBuffer containing the contents of the .wasm file.
            dracoDecoderType.wasmBinary = req.response;
            resolve();
          } else {
            this._onLoadError(DRACO_DECODER_WASM, req.statusText);
          }
        }
      };

      req.send();
    });
  }

  load = () => {
    const loader = new DRACOLoader();
    loader.setDracoDecoderType(dracoDecoderType);

    const req = new XMLHttpRequest();
    req.open('GET', this.asset.src, true);
    req.responseType = 'arraybuffer';

    req.onreadystatechange = () => {
      if (req.readyState === 4) {
        if (req.status === 200) {
          loader.decodeDracoFile(req.response, bufferGeometry => {
            this.asset.data = bufferGeometry;
            this.emit('loaded', this.asset);
          });
        } else {
          this._onLoadError(this.asset.src, req.statusText);
        }
      }
    };

    req.send();
  };

  _onLoadError = (src, error = '') => {
    this.emit('error', `Failed to load ${src} ${error}`);
  };
}



