import {
  // GridHelper,
  // AxisHelper,
  Vector3,
  Clock,
  Mesh,
  BoxBufferGeometry,
  MeshLambertMaterial,
  Object3D,
  PlaneBufferGeometry,
  Matrix4,
  // Math as MathUtils,
  DoubleSide
} from 'three';
import FastClick from 'fastclick';
import { log, warn, info } from 'utils/console';
import DracoLoader from 'utils/loaders/draco-loader';
import OrbitControls from 'lib/three/examples/OrbitControls';
import AssetLoader from 'utils/asset-loader';
import RenderStats from 'lib/three/render-stats';
import stats from 'lib/stats';
import * as flags from './flags';
import { guiFlags, guiScenes } from './utils/gui';
import lights from './lights';
import { cameraDev, cameraARParent, cameraAR } from './cameras';
import renderer from './renderer';
import scenes, { SCENES } from './scenes';
import { DEV_GUI } from './constants';
import AssetManager from './asset-manager';
import ASSETS from './assets';
import TouchControls from './lib/touch-controls';

window.simd_float4x4 = () => {};

class App {
  constructor() {
    log('ixviii');

    this.sceneId = SCENES.DEFAULT;

    this.clock = new Clock(true);

    // Renderer
    document.body.appendChild(renderer.domElement);

    // Lights
    Object.keys(lights).forEach(light => {
      scenes[SCENES.DEFAULT].add(lights[light]);
    });

    // Helpers
    // if (DEV_GUI) {
    // scenes[SCENES.DEFAULT].add(new GridHelper());
    // scenes[SCENES.DEFAULT].add(new AxisHelper());
    // }

    // Stats
    if (DEV_GUI) {
      this._renderStats = new RenderStats();
      this._renderStats.domElement.style.position = 'absolute';
      this._renderStats.domElement.style.left = '0px';
      this._renderStats.domElement.style.top = '48px';
      document.body.appendChild(this._renderStats.domElement);
      document.body.appendChild(stats.domElement);
    }

    // Controls
    this.controls = {
      dev: new OrbitControls(cameraDev, renderer.domElement)
    };

    this.touchControls = new TouchControls(renderer.domElement);

    // Camera position
    this.zoom(cameraDev, 6);

    // Gui
    guiFlags.add(flags, 'cameraDev');
    guiScenes.add(this, 'sceneId', SCENES).listen();

    scenes[SCENES.DEFAULT].add(cameraARParent);

    window.onARFrame = this.onARFrame;

    new DracoLoader().setup().then(this._onDracoSetup).catch(error => {
      console.log('Error setting up Draco', error); // eslint-disable-line
    });

    this.data = {
      anchors: []
    };

    this.meshes = {};
  }

  parseSwiftMatrix4(str) {
    const result = str.replace(/simd_float4x4\(/g, '');
    return result.replace(/\)/g, '');
  }

  parseSwiftFloat3(str) {
    return str.replace(/float3\(/g, '[');
  }

  copyMatrix4Elements(matrix1, matrix2) {
    matrix1.elements[0] = matrix2[0][0];
    matrix1.elements[1] = matrix2[0][1];
    matrix1.elements[2] = matrix2[0][2];
    matrix1.elements[3] = matrix2[0][3];
    matrix1.elements[4] = matrix2[1][0];
    matrix1.elements[5] = matrix2[1][1];
    matrix1.elements[6] = matrix2[1][2];
    matrix1.elements[7] = matrix2[1][3];
    matrix1.elements[8] = matrix2[2][0];
    matrix1.elements[9] = matrix2[2][1];
    matrix1.elements[10] = matrix2[2][2];
    matrix1.elements[11] = matrix2[2][3];
    matrix1.elements[12] = matrix2[3][0];
    matrix1.elements[13] = matrix2[3][1];
    matrix1.elements[14] = matrix2[3][2];
    matrix1.elements[15] = matrix2[3][3];
  }

  parseSimdFloat4x4(str) {
    return JSON.parse(
      str.replace(/simd_float4x4\((.+)\)/g, '$1').replace(/\)/g, '')
    );
  }

  parseFloat3(str) {
    return JSON.parse(str.replace(/float3\((.+)\)/g, '[$1]'));
  }

  onARFrame = json => {
    const data = JSON.parse(json);

    data.cameraTransform = this.parseSimdFloat4x4(data.cameraTransform);
    data.cameraProjection = this.parseSimdFloat4x4(data.cameraProjection);
    data.matrixWorldInverse = this.parseSimdFloat4x4(data.matrixWorldInverse);

    for (let i = 0; i < data.anchors.length; i += 1) {
      if (data.anchors[i].type === 'ARPlaneAnchor') {
        data.anchors[i].center = this.parseFloat3(data.anchors[i].center);
        data.anchors[i].extent = this.parseFloat3(data.anchors[i].extent);
      }
      data.anchors[i].transform = this.parseSimdFloat4x4(
        data.anchors[i].transform
      );
    }

    this.data = data;

    this._update();
    this._render();
  };

  _onDracoSetup = () => {
    this._loadAssets().then(this._onAssetsLoaded).catch(error => {
      warn('error creating scene', error);
    });
  };

  _loadAssets() {
    info('load assets');
    return new Promise((resolve, reject) => {
      AssetLoader('default', ASSETS)
        .then(response => {
          AssetManager.set(response);
          resolve();
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  _onAssetsLoaded = () => {
    this._setupScene().then(this._onSceneReady).catch(error => {
      warn('error creating scene', error);
    });
  };

  _setupScene() {
    return new Promise((resolve, reject) => {
      try {
        resolve();
      } catch (error) {
        reject(error);
      }
    });
  }

  _onSceneReady = () => {
    info('scene ready');
    this._updateFunction = this._update.bind(this);
    this._bindListeners();
    this._onResize();
    this._update();
  };

  _bindListeners() {
    window.addEventListener('resize', this._onResize, false);
    this.touchControls.on('end', this.onTouch);
  }

  onTouch = () => {
    try {
      window.webkit.messageHandlers.touchCallbackHandler.postMessage('touch');
    } catch (err) {
      console.log('The native context does not exist yet'); // eslint-disable-line
    }
  };

  zoom(camera, zoom) {
    camera.position.set(1 * zoom, 0.75 * zoom, 1 * zoom);
    camera.lookAt(new Vector3());
  }

  _update() {
    // const delta = this.clock.getDelta();

    lights.ambient.intensity = this.data.ambientIntensity;

    if (
      this.data.cameraTransform &&
      this.data.matrixWorldInverse &&
      this.data.cameraProjection
    ) {
      this.copyMatrix4Elements(
        cameraARParent.matrix,
        this.data.cameraTransform
      );
      this.copyMatrix4Elements(
        cameraAR.matrixWorldInverse,
        this.data.matrixWorldInverse
      );
      this.copyMatrix4Elements(
        cameraAR.projectionMatrix,
        this.data.cameraProjection
      );
    }

    this.data.anchors.forEach(anchor => {
      if (anchor.type === 'ARPlaneAnchor') {
        if (this.meshes[anchor.uuid] === undefined) {
          this.addPlaneMesh(anchor);
        } else {
          this.updatePlaneMesh(anchor);
        }
      }
      if (anchor.type === 'ARAnchor') {
        if (this.meshes[anchor.uuid] === undefined) {
          this.addMesh(anchor);
        } else {
          this.updateMesh(anchor);
        }
      }
    });
  }

  addMesh(anchor) {
    console.log('adding', anchor.uuid); // eslint-disable-line

    const size = 0.077;
    this.meshes[anchor.uuid] = new Object3D();

    const mesh = new Mesh(
      new BoxBufferGeometry(size, size, size),
      new MeshLambertMaterial({
        color: 0xffffff * Math.random(),
        wireframe: false
      })
      // new MeshLambertMaterial({ color: 0xffffff, wireframe: true })
    );

    // const scale = MathUtils.lerp(1, 2, Math.random())
    // mesh.scale.set(scale, scale, scale)

    this.meshes[anchor.uuid].add(mesh);
    this.meshes[anchor.uuid].matrixAutoUpdate = false;
    this.copyMatrix4Elements(this.meshes[anchor.uuid].matrix, anchor.transform);

    scenes[SCENES.DEFAULT].add(this.meshes[anchor.uuid]);
  }

  addPlaneMesh(anchor) {
    // console.log('extent', anchor.extent);

    console.log('adding', anchor.uuid); // eslint-disable-line
    this.meshes[anchor.uuid] = new Object3D();

    // new MeshLambertMaterial({ color: 0xffffff, wireframe: true })
    const geometry = new PlaneBufferGeometry(1, 1);
    geometry.applyMatrix(new Matrix4().makeRotationX(Math.PI / 2));
    const mesh = new Mesh(
      geometry,
      new MeshLambertMaterial({
        color: 0x0000ff,
        wireframe: true,
        side: DoubleSide
      })
    );

    mesh.position.fromArray(anchor.center);
    mesh.scale.x = anchor.extent[0];
    mesh.scale.z = anchor.extent[2];

    this.meshes[anchor.uuid].add(mesh);
    this.meshes[anchor.uuid].matrixAutoUpdate = false;
    this.copyMatrix4Elements(this.meshes[anchor.uuid].matrix, anchor.transform);

    scenes[SCENES.DEFAULT].add(this.meshes[anchor.uuid]);
  }

  updateMesh(anchor) {
    this.copyMatrix4Elements(this.meshes[anchor.uuid].matrix, anchor.transform);

    // this.meshes[uuid].children[0].rotation.x += delta;
    // this.meshes[uuid].children[0].rotation.y += delta;
    // this.meshes[uuid].children[0].rotation.z += delta;
  }

  updatePlaneMesh(anchor) {
    this.copyMatrix4Elements(this.meshes[anchor.uuid].matrix, anchor.transform);
    this.meshes[anchor.uuid].position.fromArray(anchor.center);
    this.meshes[anchor.uuid].scale.x = anchor.extent[0];
    this.meshes[anchor.uuid].scale.z = anchor.extent[2];
  }

  _render() {
    if (DEV_GUI) {
      stats.begin();
    }

    renderer.render(scenes[this.sceneId], cameraAR);

    if (DEV_GUI) {
      this._renderStats.update(renderer);
      stats.end();
    }
  }

  _onResize = () => {
    cameraDev.aspect = window.innerWidth / window.innerHeight;
    cameraDev.updateProjectionMatrix();

    renderer.setSize(window.innerWidth, window.innerHeight);
  };
}

document.addEventListener(
  'DOMContentLoaded',
  () => {
    FastClick.attach(document.body);
  },
  false
);

export default new App();



