export const DEV = process.env.DEV;
// export const DEV_GUI = DEV && getQueryFromParams('gui');
export const DEV_GUI = true;

let base = `${process.env.PUBLIC_URL}`;
// Add trailing slash if not present
if (base.substr(-1) !== '/') base += '/';

export const BASE_URL = base;

export const JSON_DIR = `${BASE_URL}assets/json/`;
export const AUDIO_DIR = `${BASE_URL}assets/audio/`;
export const WEBGL_DIR = `${BASE_URL}assets/webgl/`;
export const THIRD_PARTY = `${BASE_URL}assets/js/third-party/`;
export const TEXTURE_SIZE = 1024;



